</script>

<style scoped="scoped" lang="less">
  #app{
    .kuang{
      margin: 50px 200px 50px auto;
      width: 400px;
      height: 400px;
      padding: 20px;
      background: linear-gradient(to right,#ffa280,#a2a2ff,#a2a2a2);
      .content{
        width: 400px;
        height: 400px;
        background-color: #eaedf2;
      }
