import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import About from '@/components/About'
import Search from '@/components/Search'
import WorkShop from '@/components/WorkShop'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/Home'
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    {
      path: '/About',
      name: 'About',
      component: About
    },
    {
      path: '/WorkShop',
      name: 'WorkShop',
      component: WorkShop
    },
    {
      path: '/Search',
      name: 'Search',
      component: Search
    },

  ]
})



