import axios from 'axios';

axios.defaults.baseURL = 'http://localhost:3000';

//拦截器，拿到的东西处理进行输出
axios.interceptors.response.use(
  (res) => {
    return res.data;//输出res.data
  }
);


export  let getSlider = function (){
  return axios.get('/Slider');
};



